@import '../../modules';


// 공통
.custom{
  .search-table{
    margin-top: 7rem;
    margin-bottom: 2.4rem;
  }
  div.no-result{
    position: relative;
    padding: 11.5rem 0;
    margin-bottom: 10.6rem;
    border-radius: 2rem;
    background-color: $bg_main-F8F9FA;
    .result-key{
      @include style_font(700, 2.8rem, 1, $color-2C99FF_azure);
      padding-bottom: 2rem;
    }
    span{
      @include style_font(500, 2.4rem, 1, $font_color-212121_grey);
    }
    p{
      margin-top: 6.8rem;
      @include style_font(500, 1.8rem, 2em, $font_color-797979_grey);
    }
    button span{
      text-align: center;
      color: white;
      font-size: 1.4rem;
    }
  }
}

main.main-osp7111{
  .background-01{
    @include style_background-01(863px, -442px, -460px, 1298px, -175px, -770px);
    padding-top: 9.6rem;
    padding-bottom: 20.5rem;  
  }
  // 궁금한 점이 있으신가요? 
  section.background-01{
    h2{
      padding-bottom: 9.6rem;
    }
    ul{ 
      display: flex;
      gap: 3.9rem;
      justify-content: center;
      z-index: 9;
      li button{
        width: 45.4rem;
        padding: 4rem 4.7rem;
        text-align: left;
        background: white;
        box-shadow: 0rem 1rem 4rem rgba(0, 0, 0, 0.05);
        border-radius: 3rem;
        i.icon{
          width: 10rem;
          height: 10rem;
          background-position-x: -1rem;
          // icon img
          &.icon-custom-01{background-image: url('../../../../public/asset/images/icon/ico_CS-01.svg');}
          &.icon-custom-02{background-image: url('../../../../public/asset/images/icon/ico_CS-02.svg');}
          &.icon-custom-03{background-image: url('../../../../public/asset/images/icon/ico_CS-03.svg');}
        }
        h6{
          padding-top: 1.5rem;
          padding-bottom: 1.8rem;
          @include style_font(700, 2.8rem, 1.4em, #1f1f1f);
        }
        span{
          @include style_font(400, 2rem, 1.6em, #848484);
          width: 28rem;
        }
      }
    }
  }
  // 도움이 필요한 제품을 선택하세요.
  section.custom-prods{
    padding-top: 9.8rem;
    padding-bottom: 11.2rem;
    h3{
      padding-bottom: 9.8rem;
    }
    ul{
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 3.9rem;
      button{
        padding-left: 4rem;
      }
      li, button{
        width: 45.4rem;
        height: 17.2rem;
        text-align: left;
        @include style_font(700, 3.6rem, 1, white);
        background: linear-gradient(270deg, #44BCFF 0%, #4BA5FF 100%);
        border-radius: 3rem;
      }
    }
  }

  div.board-wrap{
    box-shadow: 0px 10px 40px rgba(0, 0, 0, 0.05);
    border-radius: 3rem;
    padding-bottom: 4.8rem;
    ul.cont-ul{
      padding: 0 4.4rem;
      margin-bottom: 4.8rem;
      div.--col1{
        padding-left: 2.6rem;
        justify-self: self-start;
      }
    }
  }
  section.custom-notice{
    padding-top: 9.6rem;
    padding-bottom: 7.6rem;
    background-color: $bg_main-F8F9FA;
    div.board-wrap.notice{
      padding-top: 1.6rem;
    }
  }
  section.custom-question{
    padding-top: 9.6rem;
    padding-bottom: 5.6rem;
    background-color: $bg_main-F8F9FA;
    div.board-wrap.question ul.cont-ul{
      padding-top: 3.6rem;
    }
  }
}


main.main-osp7126{
  .width1218{
    width: 1218px;
    margin: 0 auto;
  }
  .background-01{
    @include style_background-01(1194px, -321px, -291px, 900px, -246px, -405px);
  }
  section.content{
    position: relative;
    padding-top: calc(9.6rem - 1em);
    h2{
      line-height: 1.6em;
      strong{
        color: $color-2C99FF_azure;
      }
    }
    ul.products{
      z-index: 999;
      padding-bottom: 18rem;
      > li{
        display: flex;
        justify-content: space-between;
        align-items: center;

        padding: 5.2rem 6.4rem 4.6rem 6.4rem;
        margin-bottom: 5rem;
        background: #FFFFFF;
        box-shadow: 0px 10px 40px rgba(0, 0, 0, 0.05);
        border-radius: 30px;
      }
      li:first-child{
        div.download-cont{
          p{
            width: 60rem;
            padding-bottom: 3.6rem;

          }
        }
      }
      div.download-cont{
        text-align: left;
        flex: 2 2 auto;
        h4{
          height: 4rem;
          @include style_font(700, 3rem, 1, black);
        }
        p{
          @include style_font(400, 1.8rem, 2em, #212121);
          padding-top: 1.7rem;
        }
        div.tmaxgr-spec{
          @include style_font(700, 1.4rem, 1, #91989D);
          ul{
            padding-left: 4rem;
            display: inline-flex;
            flex-wrap: wrap;
            width: 80%;
            li{
              width: 50%;
              padding-bottom: 1.4rem;
              span.spec-tit{
                width: 6rem;
                font-weight: 700;
              }
              span{
                padding-left: 2.4rem;
                font-weight: 400;
              }
            }
          }
        } 
      }
      div.download-btn{
        a.btn{
          display: flex;
          justify-content: space-between;
          align-items: center;
          width: 20.8rem;
          padding: 1.1rem 1.9rem 1.1rem 2.4rem;
          margin-top: 2.4rem;
        }
        .icon-open_in_new{
          width: 1.6em;
          height: 2.4rem;
          svg{
            width: 1.2em;
            height: 2.4rem;
            path{
              fill: white;
            }
          }
        } 
      }
    }
  }
}

