@import '../../modules';


section.section-osp9113{
  background-color: white;
  div.mypage-wrap{
    background-color: $bg_main-F8F9FA;
    h4, h5{
      @include style_font(500, 2rem, 1.2em, #3C3C3C);
      margin-bottom: 2rem;
      i.icon{
        width: 2.4rem;
        height: 2.4rem;
        margin-right: 1.6rem;
      }
    }
    h5{
      padding-top: 6.8rem;
    }
    ul{
      flex-direction: column;
      margin-left: 5.6rem;
      li{
        list-style-type: disc;
        @include style_font(400, 1.6rem, 1.6em, #666);
      }
    }
  }
  div.tools{
    padding-bottom: 4rem;
    div.--l{
      a{
        margin-right: 2rem;
      }
    }
  }
}

section.section-osp9114{
  background-color: white;
  div.mypage-wrap{
    background-color: $bg_main-F8F9FA;
  }
}

section.section-osp9115{
  text-align: center;
  padding-bottom: 14.8rem;
  div.tit{
    h3{
      padding-bottom: 4rem;
    }
    padding-bottom: 13.3rem;
  }
  div.mypage-signout{
    position: relative;
    display: inline-block;
    width: 663px;
    height: 284px;
    padding-top: 11.8rem;
    margin-bottom: 13rem;
    margin-right: 3rem;

    background-color: white;
    box-shadow: 0px 20px 50px rgba(0, 129, 255, 0.1);
    border-radius: 2rem;

    &::before{
      position: absolute;
      top: -2.4rem;
      left: 4.4rem;
      width: 6rem;
      height: 6rem;
      content: '';
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;
      background-image: url('../../../../public/asset/images/icon/ico_quotes.svg');
    }
    p{
      letter-spacing: 0;
      @include style_font(400, 1.6rem, 1.4em, #666);
      strong{
        color: $color-2C99FF_azure;
        font-weight: 700;
      }
      b{
        font-weight: 700;
      }
    }
    span.img_signout{
      position: absolute;
      top: -17.4rem;
      right: -28.9rem;
      width: 40rem;
      height: 63.8rem;
      background-image: url('../../../../public/asset/images/img_bye.png');
      background-size: cover;
      background-repeat: no-repeat;
      background-position: center;
    }
  }

}