@import '../modules';


div.pagination{
  display: flex;
  justify-content: center;
  button.ar{
    width: 2rem;
    height: 3rem;
    &.--l{
      transform: rotate(180deg);
    }
  }
  ol{
    display: inline-flex;
    align-items: center;
    li button{
      width: 3.6rem;
      height: 3rem;
      color: $font_color-797979_grey;
    }
    li.now button{
      color: $color-2C99FF_azure;
      font-weight: 700;
    }
  }
}
div.pagination.commu{
  transform: translateY(-1rem);
  ol li.now button{
    color: $color-0AB4E2_commu-01;
  }
}