// 작성자: 정수진C
@import '../modules';

.custom{
  section{
    text-align: center;
    h2, h3{
      @include style_font(700, 3.6rem, 1, #1f1f1f);
      padding-bottom: 7.4rem;
    }
  }
  .pagination{
    margin-top: 4.8rem;
  }
  // * asset
  // 오잉 이미지
  span.img-no-result{
    position: absolute;
    bottom: 0;
    left: -7.1rem;
    display: block;

    width: 20.4rem; 
    height: 29.8rem;
  }
  // 검색 결과 없을 때, 문의하기 버튼
  button.btn-no-result{
    width: 10.9rem;
    height: 3.4rem;
    text-align: center;

    padding-top: 0.6rem;
    padding-left: 0.8rem;
    margin: 0 2rem;
    border-radius: 5rem;
    background: linear-gradient(270deg, #44BCFF 0%, #4BA5FF 100%);
    span{
      text-align: center;
      color: white;
      font-size: 1.4rem;
    }
    i.icon{
      margin-left: 0;
      background-image: none;
      vertical-align: -0.2em;
    }
  }
  // 목록보기 버튼 
  a.link-list{
    height: 5.8rem;
    padding: 1.4rem 4.4rem;

    border: 1px solid #B0B0B0;
    border-radius: 4rem;

    display: inline-flex;
    align-items: center;
    margin-top: 4.4rem;
    i.icon{
      margin-right: 1rem;
    }
  }

  // * 공지사항 + 묻고답하기   
  // 공지사항 & 묻고 답하기 공통
  div.board-wrap{
    background-color: white;
    ul.cont-ul{
      li{
        border-bottom: 1px solid #ececec;
        text-align: center;
        a.cont{ 
          @include style_font(400, 2rem, 1, #212121);
          // display: inline-block;
          position: relative;
          padding: 3rem 0;
          margin-left: 10rem;
          text-align: left;

          .icon-table_new{
            margin-left: 6px;
          }
          // 게시물이 공지사항 일 때, 
          &.notice::before{
            position: absolute;
            content: '';
            top: 1.2em;
            left: -5rem;
            width: 2.8rem;
            height: 2.8rem;
            vertical-align: middle;
            background-image: url('../../../public/asset/images/icon/ico_table_notice.svg');
          }
        }
        span.date{
          @include style_font(400, 1.8rem, 1, #797979);
        }
      }
    }
  }

  // 공지사항
  div.board-wrap.notice{
    div.title{
      display: grid;
      grid-template-columns: 10% auto 12%;
      column-gap: 8px;
    }
    ul.cont-ul{
      li{
        div.--col1{
          justify-self: center;
          .cate{
            @include style_font(400, 1.4rem, 1, black);
            padding: 1.1rem 2.2rem;
            background-color: #eaeaea;
            border-radius: 3rem;
            &.--pr{
              background-color: #DCE1FF;
            }
          }
        }
        display: grid;
        grid-template-columns: 10% auto 12%;
        align-items: center;
        column-gap: 8px;
        height: 9.5rem;
        span.date{
          justify-self: center;
        }
      }
    }
  }

  // 묻고 답하기 
  div.board-wrap.question{
    ul.cont-ul{
      // 레이아웃
      li{
        display: grid;
        grid-template-columns: 12% 86%;
        grid-template-areas: 'tit tit' 'cnt cnt';
        align-items: center;
        column-gap: 8px;
        position: relative;
        overflow: hidden;
      }

      div.--col1{
        padding-left: 2.6rem;
        width: max-content;
        justify-items: self-start;
        .cate{
          font-size: 1.4rem;
          padding: 1.1rem 2.2rem;
          background-color: #eaeaea;
          border-radius: 3rem;
          background-color: #CFE9FD;
          &.--grsd{
            background-color: #CCF7F7;
          }
          &.--srds{
            background-color: #D3FFDF;
          }
          &.--etc{
            background-color: #EAEAEA;
          }
        }
      }
      a.cont{
        display: inline-flex;
        justify-content: space-between;
        align-items: center;
        height: 9.6rem;

      }
      // (토글) 문의 사항 상세 내용  
      li.--dp2{
        display: none;
        border-bottom: none;
        div.question-board{
          grid-area: cnt;
          text-align: left;
          padding: 4.4rem 4.8rem 3.2rem 4.8rem;
          background-color: $bg_main-F8F9FA;
          border-radius: 0 0 3rem 3rem;

          p{
            margin-bottom: 6.4rem;
            @include style_font(400, 2rem, 2em, #4b4b4b);
          }
          div.no-res{
            > span{
              @include style_font(400, 1.4rem, 1, $color-2C99FF_azure);
            }
            width: 32rem;
            margin-left: auto;
          }
        }
      }
      li.--dp2.active{
        display: block;
      }
    }
  }
}

