// 🏠️ OSP-3111
@import '../../modules';

// 메인화면 배너 
section.main-banner{
  position: relative;
  height: 89.6rem;
  .banner-slider{ // 메인 화면 슬라이드
    height: 100%;
    background: url('../../../../public/asset/images/main_banner.png');
    // background-position: center;
    background-position: left;
    // background-size: 100vw 46vw ;
    background-size: cover;
    background-attachment: fixed;
    background-repeat: no-repeat;
  
    .slider-content{
      position: relative;
      height: 100%;
      padding-left: 5.4rem;
      color: $font_color-white;
      h1{
        padding-top: calc(19.2rem + 8.4rem);
        @include style_font(700, 5.2rem, 1.4em, $font_color-white);
        margin-bottom: 1.6rem;
      }
      p{
        font-weight: 300;
        line-height: 1.8em;
        padding-top: 0.4rem;
        padding-bottom: 5.2rem;
      }
      div.slider-controller{
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 13rem;
        background-color: rgba(0, 0, 0, 0.08);
        .page-now{
          font-weight: 700;
        }
        .page-total{
          color: rgba(255, 255, 255, 0.4);
        }
        button i{
          width: 3rem;
          height: 3rem;
        }
      }
    }
  }
  .banner-notice{ // 메인 화면 공지사항
    position: absolute;
    bottom: 0;
    width: 100%;
    .width1440{
      display: flex;
      align-items: center;
      height: 73px;
      padding: 1em 20px;
      background-color: rgba(0, 10, 47, 0.44);
      border-radius: 1em 1em 0 0;
    }
    span{
      width: 108px;
      color: $font_color-white;
      padding: 5px 20px;
      font-weight: 700;
    }
    a{
      color: $font_color-white;
    }
    .notice-now{
      width: calc(100% - 258px);
      padding: 1em;
      font-weight: 300;
    }
    .notice-all{
      padding: 1em;
      width: 150px;
      text-align: right;
      font-weight: 500;
      .icon-more_arrow{
        margin-left: 4px;
        background-image: none;
      }
    }
  }
}

// ---------------------------------
main.main-osp3111{
  section.main-product{
    @include style_main_h3($font_color-white);
    text-align: center;
    background: linear-gradient(90.72deg, #4661FF 0.62%, #279FFF 99.38%);
    .width1440{
      h3{
        padding-bottom: 9.6rem;
      }
      padding: 9.6rem 0;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
    }
    div.product-cards{
      width: 100%;
      display: flex;
      justify-content: space-between;
      div{
        width: 462px;
        height: 645px;
        padding-top: 6px;
        padding-bottom: 57px;

        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;

        background: #FFFFFF;
        box-shadow: 0px 20px 20px rgba(0, 0, 0, 0.08);
        border-radius: 3rem;
        span.img{
          display: block;
          width: 34rem;
          height: 34rem;
          &.img_feature-1{background-image: url('../../../../public/asset/images/img_product/image-_tmaxgooroom.png');}
          &.img_feature-2{background-image: url('../../../../public/asset/images/img_product/image-_tmaxgooroom_sd.png');}
          &.img_feature-3{background-image: url('../../../../public/asset/images/img_product/image-superrds.png');}
        }
        h4{
          font-size: 32px;
          font-weight: 700;
          margin: 34px 0 36px 0;
        }
        p{
          width: 382px;
          height: 81px;
          font-size: 19px;
          line-height: 150%;
          color: $font_color-848484_grey;
        }
        button{
          margin-top: 33px;
          i{
            margin-left: 6px;
          }
        }
      }
    }
  }
}
